export default class ListFormulaCalc {
  private static _totalDigitsLength: number = 0;
  private static _formulas: string[] = [];

  public static generateTreeDigits(digitsLength: number): {
    name: number,
    operatorCount: number,
    accumulatedOperatorCount: number,
    treeDigits: any,
  }[] {
    ListFormulaCalc._totalDigitsLength = digitsLength;
    let treeDigits: {
      name: number,
      operatorCount: number,
      accumulatedOperatorCount: number,
      treeDigits: any,
    }[] = [];
    let minOperatorCount: number = 1;
    let maxOperatorCount: number = digitsLength - 1;
    for (let i: number = minOperatorCount; i <= maxOperatorCount; i++) {
      treeDigits.push({
        name: digitsLength - 1,
        operatorCount: i,
        accumulatedOperatorCount: i,
        treeDigits: null,
      });
    }
    ListFormulaCalc.generateTreeDigitsRecursively(treeDigits, digitsLength);
    return treeDigits;
  }

  private static generateTreeDigitsRecursively(treeDigits: {
    name: number,
    operatorCount: number,
    accumulatedOperatorCount: number,
    treeDigits: any,
  }[], digitsLength: number): void {
    if (1 < digitsLength) {
      let childDigitsLength: number = digitsLength - 1;
      let childMaxOperatorCount: number = childDigitsLength - 1;
      for (let i: number = 0; i < treeDigits.length; i++) {
        let childTreeDigits: {
          name: number,
          operatorCount: number,
          accumulatedOperatorCount: number,
          treeDigits: any,
        }[] = [];
        let restOperatorCount: number = ListFormulaCalc._totalDigitsLength - treeDigits[i].accumulatedOperatorCount - 1;
        let childMinOperatorCount: number = 0;
        if (0 < restOperatorCount && restOperatorCount === childMaxOperatorCount) {
          childMinOperatorCount = 1;
        }
        for (let j: number = childMinOperatorCount; j <= restOperatorCount; j++) {
          childTreeDigits.push({
            name: childDigitsLength - 1,
            operatorCount: j,
            accumulatedOperatorCount: treeDigits[i].accumulatedOperatorCount + j,
            treeDigits: null,
          });
          ListFormulaCalc.generateTreeDigitsRecursively(childTreeDigits, childDigitsLength);
        }
        treeDigits[i].treeDigits = childTreeDigits;
      }
    }
  }

  public static rollTreeDigits(treeDigits: {
    name: number,
    operatorCount: number,
    accumulatedOperatorCount: number,
    treeDigits: any,
  }[]): string[] {
    ListFormulaCalc._formulas.length = 0;
    ListFormulaCalc.rollTreeDigitsRecursively(treeDigits, '');
    return ListFormulaCalc._formulas;
  }

  private static rollTreeDigitsRecursively(treeDigits: {
    name: number,
    operatorCount: number,
    accumulatedOperatorCount: number,
    treeDigits: any,
  }[], suffix: string): void {
    const UNSETTLED_OPERATOR = '#'
    for (let i: number = 0; i < treeDigits.length; i++) {
      if (treeDigits[i].name === 0) {
        this._formulas.push(String(treeDigits[i].name) + UNSETTLED_OPERATOR.repeat(treeDigits[i].operatorCount) + ' ' + suffix);
      } else {
        this.rollTreeDigitsRecursively(
          treeDigits[i].treeDigits, 
          String(treeDigits[i].name) + UNSETTLED_OPERATOR.repeat(treeDigits[i].operatorCount) + ' ' + suffix
        );
      }
    }
  }
}