


























































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import CButton from '@/components/CButton.vue';
import ListFormulaCalc from '@/typescript/ListFormulaCalc.ts';

@Component({
  components: {
    CButton,
  },
  head: {
    title: {
      inner: '式の列挙',
    },
  },
})
export default class ListFormula extends Vue {
  // Data
  private static readonly MIN_DIGIT_COUNT: number = 2;
  private static readonly MAX_DIGIT_COUNT: number = 10
  private dataButtonText: string = '探索';
  private dataDigitCount: number = 4;
  private dataDigitCounts: number[] = [];
  private dataFormulas: string[] = [];
  private dataAllFormulasCount: number = 0;

  created(): void {
    this.dataDigitCounts = new Array<number>(
      ListFormula.MAX_DIGIT_COUNT + 1 - ListFormula.MIN_DIGIT_COUNT
    ).fill(ListFormula.MIN_DIGIT_COUNT).map((value, index) => {
      return ListFormula.MIN_DIGIT_COUNT + index;
    });
  }

  // Methods
  private listenOnSelectDigitCount($event: Event): void {
    this.dataDigitCount = Number((<HTMLInputElement>(<any>$event).target).value);
  }
  private generateTreeDigits(): void {
    let treeDigits: {
      name: number,
      operatorCount: number,
      accumulatedOperatorCount: number,
      treeDigits: any,
    }[] = ListFormulaCalc.generateTreeDigits(this.dataDigitCount);
    let formulas: string[] = ListFormulaCalc.rollTreeDigits(treeDigits);
    formulas = formulas.map((value) => {
      return value.replace(/\#/g, '-');
    });
    this.dataFormulas = formulas;
    this.countAllFormulas();
  }
  private countAllFormulas(): void {
    this.dataAllFormulasCount = this.dataFormulas.length * (4 ** (this.dataDigitCount - 1));
  }
}
